<template>
  <div class="user-wrap">
    <div class="user-header">
      <div class="header-name">
        <img 
          class="user-avatar fl" 
          :src="info.avatar" 
          alt=""
        />
        <div class="info fr">
          <p class="name">{{info.nickName}}</p>
          <p class="points">手机号：{{info.tel}}</p>
          <p class="points">信用积分：{{info.points}}</p>
        </div>
      </div>
      <div class="header-money">
        <van-row>
          <van-col span="8">
            <p>钱包余额</p>
            <p>{{info.money}}</p>
          </van-col>
          <van-col span="8">
            <p>累计提现</p>
            <p>{{info.flozen}}</p>
          </van-col>
          <van-col span="8">
            <p>冻结金额</p>
            <p>{{info.changed}}</p>
          </van-col>
        </van-row>
      </div>
      <van-icon 
        class="setting"
        name="eye-o" 
        @click="closeEye"
        v-if="eyeClose === false"
        size="24"
      />
      <van-icon 
        class="setting"
        name="closed-eye" 
        @click="openEye"
        v-if="eyeClose === true"
        size="24"
      />
    </div>
    <van-row class="user-links">
      <van-col span="2"></van-col>
      <van-col span="4" @click="goPage('/orders', 'a')">
        <van-icon name="records" />
        <p class="text">交易中</p>
      </van-col>
      <van-col span="4" @click="goPage('/orders', 'b')">
        <van-icon name="todo-list-o" />
        <p class="text">纠纷中</p>
      </van-col>
      <van-col span="4" @click="goPage('/orders', 'c')">
        <van-icon name="tosend" />
        <p class="text">审核中</p>
      </van-col>
      <van-col span="4" @click="goPage('/orders', 'd')">
        <van-icon name="close" />
        <p class="text">已关闭</p>
      </van-col>
      <van-col span="4" @click="goPage('/orders', 'e')">
        <van-icon name="smile-o" />
        <p class="text">已完成</p>
      </van-col>
      <van-col span="2"></van-col>
    </van-row>
    <van-cell-group>
      <van-cell icon="balance-o" title="交易查询" is-link @click="goPage('/orders')"/>
      <van-cell icon="orders-o" title="公告" is-link @click="goPage('/notice')" />
      <van-cell icon="records" title="会员协议" is-link @click="goPage('/agreement')" />
    </van-cell-group>
    <van-cell-group>
      <van-cell icon="friends-o" title="商务合作" is-link @click="goPage('/qscode')">微信：{{wechat}}</van-cell>
      <van-cell icon="service-o" title="客服" is-link @click="goPage('/qscode')">微信：{{service}}</van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell icon="description" title="常见问题" is-link @click="goPage('/questions')" />
      <van-cell icon="smile-comment-o" title="意见反馈" is-link @click="goPage('/callback')" />
      <van-cell icon="bullhorn-o" title="消息推送" is-link @click="goPage('/msgSetting')" />
      <van-cell icon="setting-o" title="设置" is-link @click="goPage('/setting')" />
    </van-cell-group>
    <TabBar />
  </div>
</template>

<script>
import TabBar from '@/components/TabBar'
import avatar_default from '@/assets/images/avatar_default.png'

export default {
  name: 'index',
  components: {
    TabBar
  },
  data () {
    return {
      info: {
        nickName: '百杰票务',
        avatar: 'https://img.yzcdn.cn/vant/leaf.jpg',
        tel: 13043334444,
        points: 20,
        money: '¥2312',
        flozen: '¥123',
        changed: '¥23232'
      },
      eyeClose: false,
      service: '13423423423',
      wechat: '23131231231'
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    closeEye () {
      this.eyeClose = true
      this.info.money = '****'
      this.info.flozen = '****'
      this.info.changed = '****'
    },
    openEye () {
      this.eyeClose = false
      this.info.money = '¥' + 2312
      this.info.flozen = '¥' + 123
      this.info.changed = '¥' + 23232
    },
    goPage (url, type) {
      this.$router.push({ path: url, query: {type: type} })
    }
  }
}
</script>



